const phoneRegex = /^(?:(?:\+|00)86)?1\d{10}$/ // 
// 
// const emailRegex = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
const urlRegex = /^(((ht|f)tps?):\/\/)?[\w-]+(\.[\w-]+)+([\w.,@?^=%&:/~+#-]*[\w@?^=%&/~+#-])?$/
// 
const chRegex = /^[\u4e00-\u9fa5]{0,}$/
// 
const ageRegx = /^[0-9]*$/

const formRules = {
  username: [
    { required: true, message: '' },
    { min: 6, max: 18, message: ' 6  18 ', trigger: 'blur' }
  ],
  // 
  email: [{ type: 'email', message: '', trigger: 'blur' }],
  // 
  name: [
    { required: true, message: '', trigger: 'blur' },
    {
      validator: function (rule, value, callback) {
        if (chRegex.test(value) === false) {
          callback(new Error(''))
        } else {
          // 
          callback()
        }
      },
      trigger: 'blur'
    }
  ],
  password: [
    {
      required: true,
      message: '',
      trigger: 'blur'
    },
    { min: 6, max: 16, message: '6-16', trigger: 'blur' }
  ],
  // 
  mobile: [
    { required: true, message: '', trigger: 'blur' },
    {
      validator: function (rule, value, callback) {
        if (phoneRegex.test(value) === false) {
          callback(new Error(''))
        } else {
          callback()
        }
      },
      trigger: 'blur'
    }
  ],
  // 
  age: [
    {
      validator: function (rule, value, callback) {
        if (ageRegx.test(value) === false) {
          callback(new Error(''))
        } else {
          callback()
        }
      },
      trigger: 'blur'
    }
  ],
  // 
  url: [
    {
      validator: function (rule, value, callback) {
        if (urlRegex.test(value) === false) {
          callback(new Error('Url'))
        } else {
          callback()
        }
      },
      trigger: 'blur'
    }
  ],
  checkCar: [
    {
      validator: (rule, value, callback) => {
        if (!value) {
          return callback()
        } else {
          let result = false
          if (value.length === 7) {
            // 7 1+1+4+1  :AAAAAAA  A9999
            const express = /^[A-Z]{1}[A-Z]{1}[A-Z0-9]{4}[A-Z0-9]{1}$/
            result = express.test(value) //  true
          } else if (value.length === 8) {
            const express2 = /^[A-Z]{1}[A-Z]{1}[A-Z0-9]{5}[A-Z0-9]{1}$/
            result = express2.test(value)
          }
          if (result) {
            return callback()
          } else {
            return callback(new Error(''))
          }
        }
      }
    }
  ],
  // 
  cardId: [
    { required: true, message: '', trigger: 'blur' },
    {
      pattern: /(^[1-9]\d{5}(18|19|([23]\d))\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$)|(^[1-9]\d{5}\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{2}$)/,
      message: '',
      trigger: 'blur'
    }
  ]
}
module.exports = {
  formRules
}
